import gql from 'graphql-tag'


export const Service_Query = gql`
  query getServices($start: Int, $end: Int) {
    getServices(start: $start, end: $end)  {
        id
        latitude
        longitude
        typeService
        status
        customer {
          id
          names
        }
        technical {
            id
            names
            last_name
        }
        engineer {
            id
            names
            last_name
        }
        packs_service {
          id
          quantity
          pack {
            name
            price
          }
        }
        
      },
      totalServices
  } 
`
export const Users_Query = gql`
 query getUsers {
  technicals: getPersonByRole( role : "5e1a13cb948d2f7342d617a6") {
    id
    names
  },
  engineers: getPersonByRole( role: "5e1a0dee948d2f7342d617a5") {
    id
    names
  }
}

`