import React, { Fragment, useState } from 'react'
import { useQuery, useMutation } from "@apollo/react-hooks"
import { emphasize, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';


import clsx from 'clsx';
import { Users_Query } from '../../queries/service.query'

import { Button } from '@material-ui/core';
import TextField from "@material-ui/core/TextField";

import MenuItem from "@material-ui/core/MenuItem";


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 250,
    minWidth: 290
  },

  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,

  },
  button:
  {
    margin: theme.spacing(1),
  },
  formControl: {
    margin: theme.spacing(1),
  },
}))

const ServiceAddWorkers = props => {
  const classes = useStyles()
  const [engineer, setEngineer] = useState(props.service.engineer !== null ? props.service.engineer.id : '')
  const [technical, setTechnical] = useState(props.service.technical !== null ? props.service.technical.id : '')
  const [engineers, setEngineers] = useState([])
  const [technicals, setTechnicals] = useState([])
  const [disabled, setDisabled] = useState(true)

  const { data, error, loading } = useQuery(Users_Query, {
    fetchPolicy: 'no-cache',
    onCompleted: () => {
      setTechnicals(data.technicals)
      setEngineers(data.engineers)
    }
  })

  if (loading) return 'Loading...'

  const onChangeEngineer = e => {
    setEngineer(e.target.value)
    checkBnt()

  }

  const onChangeTechnical = e => {
    setTechnical(e.target.value)
    checkBnt()
  }

  const setPersons = e => {

    let technicalSelected = technicals.filter(currentTechical => currentTechical.id = technical)
    let engineerSelected = engineers.filter(currentEngineer => currentEngineer.id = engineer)

    console.log('technicalSelected', technicalSelected)
    console.log('engineerSelected', engineerSelected)

    if (technicalSelected) {
      technicalSelected = technicalSelected[0]
    }
    if (engineerSelected) {
      engineerSelected = engineerSelected[0]
    }
    props.setPersons(technicalSelected, engineerSelected)
  }

  const checkBnt = () => {
    console.log('props.typeService', props.service)
    if (props.service.typeService === 1) {
      if (technical.length > 0) {
        setDisabled(false)
      }
    }
    else {
      console.log('technical', technical)
      console.log('engineer', engineer)

      if (technical.length > 0 && engineer.length > 0) {
        console.log(' ingreso ...  ')
        setDisabled(false)
      }
    }
  }

  return (
    <Fragment>

      <Container maxWidth="md">

        <Paper className={classes.paper}>
          <Grid container spacing={3}>


            <Grid item xs={12} sm={6}>
              <TextField fullWidth
                disabled={props.service.typeService == 1 ? true : false}
                select
                className={clsx(classes.margin, classes.textField)}
                variant="outlined"
                label="Ingeniero"
                value={engineer}

                onChange={onChangeEngineer}
              >
                {
                  engineers.map(option => (
                    <MenuItem key={option.id} value={option.id}>
                      {option.names}
                    </MenuItem>))
                }
              </TextField>
            </Grid>

            <Grid item xs={12} sm={6}>
              <TextField fullWidth
                select
                className={clsx(classes.margin, classes.textField)}
                variant="outlined"
                label="Tcnico"
                value={technical}
                onChange={onChangeTechnical}
              >
                {
                  technicals.map(option => (
                    <MenuItem key={option.id} value={option.id}>
                      {option.names}
                    </MenuItem>))
                }
              </TextField>
            </Grid>


            <Grid item xs={2} sm={3}>
              <Button /*disabled={disabled}*/ onClick={setPersons} type="submit" variant="contained" color='primary'>Asignar</Button>
            </Grid>
          </Grid>
        </Paper>

      </Container>
    </Fragment>
  )
}

export default ServiceAddWorkers