import React, { useState, Fragment, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import { Service_Query } from '../../queries/service.query'
import ServiceAddWorkers from './ServiceAddWorkers'
import { esES } from '@material-ui/core/locale';
import Moment from 'react-moment';
import moment from 'moment';
import Tooltip from '@material-ui/core/Tooltip';
import { useLazyQuery } from '@apollo/react-hooks';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import SearchIcon from '@material-ui/icons/Search';
import Button from '@material-ui/core/Button';
import NumberFormat from 'react-number-format';
import Swal from 'sweetalert2'
import RoomIcon from '@material-ui/icons/Room';
import ShoppingBasketIcon from '@material-ui/icons/ShoppingBasket';
import GroupAddIcon from '@material-ui/icons/GroupAdd';
import Chip from '@material-ui/core/Chip';
import FaceIcon from '@material-ui/icons/Face';

import { useMutation } from "@apollo/react-hooks"
import { Set_Person_To_Service } from '../../mutations/Service/service'
import Map from "../Services/ServiceMapLocation";
import Slide from '@material-ui/core/Slide';


import { connect } from 'react-redux'
import { isLoading } from '../../actions'

import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
const theme = createMuiTheme({}, esES);

moment().locale('es')
const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  container: {
    maxHeight: 440,
  },

  margin: {
    margin: theme.spacing(1),
  },
  green: {
    color: "green",
  },
  icon: {
    fontSize: 32
  }
});


const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
})
const Services = props => {


  useEffect(() => {
    getServices({ variables: { start: page, end: rowsPerPage } })
  }, [])
  const [getServices, { data }] = useLazyQuery(Service_Query, {
    fetchPolicy: 'no-cache',
    onCompleted: () => {
      setServicies(data.getServices)
      setTotalServices(data.totalServices)
    }
  })


  const classes = useStyles();
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);

  const [totalServices, setTotalServices] = useState(0)
  const [services, setServicies] = useState([])
  const [packs_service, setPacksService] = useState([])
  const [openDetailPacksServices, setOpenDetailPacksServices] = useState(false)
  const [openDetailLocation, setDetailLocation] = useState(false)
  const [openAddWorkers, setOpenAddWorkers] = useState(false)
  const [lat, setLat] = useState(0)
  const [lng, setLng] = useState(0)
  const [service, setService] = useState({})
  const [selectedWorkers, setSelectedWorkers] = useState([])

  const [setPersonToService] = useMutation(Set_Person_To_Service)

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    getServices({ variables: { start: newPage, end: rowsPerPage } })
  }

  const handleChangeRowsPerPage = event => {

    setRowsPerPage(+event.target.value);
    setPage(0);
    getServices({ variables: { start: 0, end: event.target.value } })
  }

  const onDetailPacksServices = packs_service => () => {
    setPacksService(packs_service)
    setOpenDetailPacksServices(true)
  }

  const onDetailLocation = (lat, lng) => () => {

    setLat(lat)
    setLng(lng)
    setDetailLocation(true)
  }

  const onCloseDetailLocation = () => {
    setDetailLocation(false)
  }

  const onAddWorkers = service => () => {
    setService(service)
    setOpenAddWorkers(true)
  }

  const onCloseAddWorkers = () => {
    setOpenAddWorkers(false)
  }

  const setPersons = (technical, engineer) => {
    onSetPersonToService(technical, engineer ? engineer : '')
  }

  const onSetPersonToService = (technical, engineer) => {

    props.onIsLoading(true)
    setPersonToService({
      variables: { service: service.id, technical: technical ? technical.id : '', engineer: engineer ? engineer.id : '' }
    }).then(response => {
      props.onIsLoading(false)
      setOpenAddWorkers(false)
      console.log('data', response.data)

      if (response.data.setPersonToService.status) {
        Swal.fire({
          icon: 'success',
          title: `Operacin realizada exitosamente`,
          showConfirmButton: false,
          timer: 1500
        })
        const newServices = services.map(currentService => {
          if (service.id !== currentService.id)
            return currentService

          currentService.technical = technical
          currentService.engineer = engineer

          return currentService
        })
        setServicies(newServices)
        setDetailLocation(false)

      } else {
        Swal.fire({
          icon: 'error',
          title: response.data.setPersonToService.message,
          showConfirmButton: false,
          timer: 5000
        })
      }

    })
  };

  const onCloseDetailProduct = () => {
    setOpenDetailPacksServices(false)
  };


  return (

    <ThemeProvider theme={theme} >
      <Fragment >

        <Paper className={classes.margin}>

          <TableContainer className={classes.container}>
            <Table stickyHeader aria-label="sticky table">
              <TableHead>
                <TableRow>
                  <TableCell>Fecha</TableCell>
                  <TableCell>Cliente</TableCell>
                  <TableCell>Tipo de servicio</TableCell>
                  <TableCell>Estado</TableCell>
                  <TableCell width={20} align={"center"}>Paquetes</TableCell>
                  <TableCell width={20} align={"center"}>Ubicacin</TableCell>
                  <TableCell width={20} align={"center"}>Asignar trabajadores</TableCell>

                </TableRow>
              </TableHead>
              <TableBody>
                {services.map(service => {
                  return (
                    <TableRow hover role="checkbox" tabIndex={-1} key={service.id}>
                      <TableCell><Moment format="ll">{service.date}</Moment></TableCell>
                      <TableCell>{service.customer.names}</TableCell>
                      <TableCell>{service.typeService === 1 ? "Residencial" : "Coorporativo"}</TableCell>
                      <TableCell>{service.status === 'canceled' ? "Cancelado" : "Activo"}</TableCell>

                      <TableCell align={"center"}>
                        <ShoppingBasketIcon className={classes.icon} onClick={onDetailPacksServices(service.packs_service)} />

                      </TableCell>
                      <TableCell align={"center"}>
                        <RoomIcon className={classes.icon} onClick={onDetailLocation(service.latitude, service.longitude)} />

                      </TableCell>

                      <TableCell align={"center"}>
                        {
                          (service.status === 'created' && !service.technical && !service.engineer) ?
                            <GroupAddIcon className={classes.icon} onClick={onAddWorkers(service)} />
                            : ''

                        }
                        {
                          service.technical ?
                            <Chip
                              key={service.technical.id}
                              icon={<FaceIcon />}
                              label={`${service.technical.names} ${service.technical.last_name ? service.technical.last_name : ''}`}
                              variant="outlined"
                            />
                            : ''
                        }
                        {
                          service.engineer ?
                            <Chip
                              key={service.engineer.id}
                              icon={<FaceIcon />}
                              label={`${service.engineer.names} ${service.engineer.last_name ? service.engineer.last_name : ''}`}
                              variant="outlined"
                            />
                            : ''
                        }
                      </TableCell>

                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
          <TablePagination
            rowsPerPageOptions={[10, 25, 100]}
            component="div"
            count={totalServices}
            rowsPerPage={rowsPerPage}
            page={page}
            onChangePage={handleChangePage}
            onChangeRowsPerPage={handleChangeRowsPerPage}
          />
        </Paper>
        <Dialog
          maxWidth="lg"
          open={openDetailPacksServices}
          onClose={onDetailPacksServices}
          aria-labelledby="draggable-dialog-title">
          <DialogTitle id="draggable-dialog-title">Paquetes</DialogTitle>
          <DialogContent>

            <Table stickyHeader aria-label="sticky table">
              <TableHead>
                <TableRow>
                  <TableCell>Nombre</TableCell>
                  <TableCell>Cantidad</TableCell>
                  <TableCell align="right">Precio</TableCell>
                  <TableCell align="right">Total</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {packs_service.map(row => {
                  return (
                    <TableRow hover role="checkbox" tabIndex={-1} key={row.id}>
                      <TableCell>{row.pack.name}</TableCell>
                      <TableCell>{row.quantity}</TableCell>
                      <TableCell align="right"><NumberFormat value={row.pack.price} displayType={'text'} thousandSeparator={true} prefix={'$'} /></TableCell>
                      <TableCell align="right"><NumberFormat value={row.quantity * row.pack.price} displayType={'text'} thousandSeparator={true} prefix={'$'} /></TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>

          </DialogContent>
          <DialogActions>

            <Button onClick={onCloseDetailProduct} color="primary">
              Cerrar
                </Button>
          </DialogActions>
        </Dialog>

        <Dialog
          TransitionComponent={Transition}
          fullWidth={true}
          open={openDetailLocation}
          onClose={onCloseDetailLocation}

          aria-labelledby="draggable-dialog-title">
          <DialogTitle id="draggable-dialog-title">
            Ubicacin del servicio
            </DialogTitle>
          <DialogContent>
            <Map
              lat={lat}
              lng={lng} />
          </DialogContent>
          <DialogActions>
            <Button autoFocus onClick={onCloseDetailLocation} color="primary">
              Cerrar
            </Button>
          </DialogActions>
        </Dialog>

        <Dialog
          fullWidth={true}
          open={openAddWorkers}
          onClose={onCloseAddWorkers}>
          <DialogTitle>
            Asignar trabajadores
            </DialogTitle>
          <DialogContent>
            <ServiceAddWorkers service={service} setPersons={setPersons} />
          </DialogContent>
          <DialogActions>
            <Button autoFocus onClick={onCloseAddWorkers} color="primary">
              Cerrar
            </Button>
          </DialogActions>
        </Dialog>



      </Fragment>
    </ThemeProvider>
  );
}

const mapStateToProps = state => {
  return {
    isLoading: state.isLoading
  }
}
const mapDispatchToProps = dispatch => {
  return {
    onIsLoading(status) {
      dispatch(isLoading(status))
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Services)