import React, { useEffect, useRef, useState } from "react";
import mapboxgl from "mapbox-gl";
import "mapbox-gl/dist/mapbox-gl.css";
import $ from 'jquery'


const Map = props => {


  const [map, setMap] = useState(null);
  const mapContainer = useRef(null);



  useEffect(() => {

    console.log('props', props)

    mapboxgl.accessToken = 'pk.eyJ1IjoibnRvcnJlczE5ODYiLCJhIjoiY2tiYnFlMjV1MDNzbDMxcnV0cGp6d2EycSJ9.TMjPubTz_kdfNPW9IpzGow';
    const initializeMap = ({ setMap, mapContainer }) => {
      const map = new mapboxgl.Map({
        container: mapContainer.current,
        style: "mapbox://styles/mapbox/streets-v11",
        center: [props.lng, props.lat],
        zoom: 12
      });

      var marker = new mapboxgl.Marker({
        draggable: true
      }).setLngLat([props.lng, props.lat]).addTo(map);

      marker.on('dragend', function () {
        var lngLat = marker.getLngLat();
        $('.service-form').show()
        // $('.coordinates').html('Longitude: ' + lngLat.lng + '<br />Latitude: ' + lngLat.lat);

      })
      map.on("load", () => {
        setMap(map);
        map.resize();
      });
    };

    if (!map) initializeMap({ setMap, mapContainer });
  }, [map])



  return (
    <div id="map" ref={el => (mapContainer.current = el)} ></div>)
}

export default Map;